interface experience {
    image: string,
    title: string,
    solution: string,
    website: string,
    sDate: string,
    eDate: string,
    totalDate: string,
    value: string[],
}

export default Vue.extend({
    data() {
        return {
            title: 'Experience' as string,
            /*
                ** Îç∞Ïù¥ÌÑ∞ ÏûÖÎ†• Ï£ºÏùòÏÇ¨Ìï≠ **
                ÌòÑÏû¨ Ïû¨ÏßÅÏ§ëÏù¥Î©¥ eDateÎäî ÎπÑÏõåÎëò Í≤É.
                totalDateÎäî ÏûêÎèôÏúºÎ°ú Í≥ÑÏÇ∞Ìï¥Ï£ºÎØÄÎ°ú Ìï≠ÏÉÅ ÎπÑÏõåÎëò Í≤É.
            */
            experiences: [
                {
                    image: require(`../../assets/expert-consulting.jpg`),
                    title: 'ÏóëÏä§ÌçºÌä∏Ïª®ÏÑ§ÌåÖ',
                    solution: 'Vue.js ÌîÑÎ°†Ìä∏ÏóîÎìú Í∞úÎ∞úÏûê',
                    website: 'https://www.exc.co.kr/',
                    sDate: '2022-01-03',
                    eDate: '',
                    totalDate: '',
                    value: [
                    'HRM Î¶¨ÌÅ¨Î£®Ìä∏Ïò® ÌîÑÎ°†Ìä∏ÏóîÎìú Í∞úÎ∞ú Î∞è Ïú†ÏßÄ Î≥¥Ïàò',
                    'ÏÑ±ÎÇ®Ïãú ÌÜµÌï© Ï±ÑÏö© ÏÇ¨Ïù¥Ìä∏ Íµ¨Ï∂ï',
                    'Í≥†ÏñëÏãú ÌÜµÌï© Ï±ÑÏö© ÏÇ¨Ïù¥Ìä∏ Íµ¨Ï∂ï',
                    'Í∏∞ÏóÖÏ±ÑÏö© ÌòÑÏû•ÏßÄÏõê ÏÇ¨ÏóÖ ÌîÑÎ°†Ìä∏ÏóîÎìú Í∞úÎ∞ú Î∞è Ïú†ÏßÄ Î≥¥Ïàò',
                    ]
                },
            ] as experience[],
        }
    },
    created () {
        this.totalDateCalculation();
    },
    methods: {
        totalDateCalculation() {
            const experiences: experience[] = this.experiences;

            for (let experience of experiences) {
                let sDate: Date, eDate: Date;

                if (experience.eDate) {
                    sDate = new Date(experience.sDate);
                    eDate = new Date(experience.eDate);
                } else { 
                    // Ïû¨ÏßÅÏ§ëÏù¥ÎùºÎ©¥ eDateÍ∞Ä ÌòÑÏû¨ ÎÇ†ÏßúÎ°ú Í∏∞Ïû¨
                    sDate = new Date(experience.sDate);
                    eDate = new Date();
                }

                if (eDate.getFullYear() - sDate.getFullYear()) { 
                    // Í≤ΩÎ†•Ïù¥ 1ÎÖÑ Ïù¥ÏÉÅÏù¥ÎùºÎ©¥
                    experience.totalDate = `${eDate.getFullYear() - sDate.getFullYear()}ÎÖÑ ${eDate.getMonth() - sDate.getMonth() + 1}Í∞úÏõî`;
                } else { 
                    // Í≤ΩÎ†•Ïù¥ 1ÎÖÑ Ïù¥ÌïòÏù¥Î©¥
                    experience.totalDate = `${eDate.getMonth() - sDate.getMonth() + 1}Í∞úÏõî`;
                }
            } // For End  
        }
    },
})
